import { Form } from "./common/Form";
export class Order extends Form {
    constructor(container, events) {
        super(container, events);
        this.button_cash = this.container.querySelector('.button_cash');
        this.button_card = this.container.querySelector('.button_card');
        this.button_cash.addEventListener('click', () => {
            this.events.emit('payment-Cash:select');
        });
        this.button_card.addEventListener('click', () => {
            this.events.emit('payment-Card:select');
        });
    }
    set address(value) {
        this.container.elements.namedItem('address').value = value;
    }
}
export class OrderContacts extends Form {
    constructor(container, events) {
        super(container, events);
    }
    set phone(value) {
        this.container.elements.namedItem('phone').value = value;
    }
    set email(value) {
        this.container.elements.namedItem('email').value = value;
    }
}
